import {NgModule, ModuleWithProviders} from '@angular/core';
import {FormsModule} from '@angular/forms';
import {CommonModule} from '@angular/common';

import {AdaptCustomActionsModule} from '../custom-actions/index';
import {AdaptDragonInputHandlerModule} from '../dragon-input-handler/index';
import {AdaptHighlightModule} from './../highlight/index';
import {AdaptLazyLoadModule} from '../common/lazy-load/index';
import {AdaptRxSearchModule} from '../rx/rx-search/index';

import {AdaptTreeComponent} from './tree.component';
import {AdaptTreeNodeComponent} from './tree-node.component';
import {AdaptTreeNodeTemplateDirective} from './tree-node-template.directive';
import {AdaptTreeNodeTemplateLoaderComponent} from './tree-template.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    AdaptDragonInputHandlerModule,
    AdaptLazyLoadModule,
    AdaptRxSearchModule,
    AdaptHighlightModule,
    AdaptCustomActionsModule
  ],
  exports: [
    AdaptTreeComponent,
    AdaptTreeNodeTemplateDirective
  ],
  declarations: [
    AdaptTreeComponent,
    AdaptTreeNodeComponent,
    AdaptTreeNodeTemplateDirective,
    AdaptTreeNodeTemplateLoaderComponent
  ],
})

export class AdaptTreeModule {
  /**
   * Importing with '.forRoot()' is no longer necessary, you can simply import the module.
   * Will be removed in a short while
   *
   * @deprecated 11.16.0
   */
  static forRoot(): ModuleWithProviders<AdaptTreeModule> {
    return {
      ngModule: AdaptTreeModule
    };
  }
}

export * from './tree.component';
export * from './tree-node.component';
export * from './tree-template.component';
export * from './tree-node-template.directive';
export * from './tree.model';
